.highwayCoffee {
  position: relative;
  font-size: var(--font-size-13xl);
  font-weight: 800;
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  -webkit-text-stroke: 1px #000;
}
.fuelYourJourney {
  margin: 0;
}
.fuelYourJourneyContainer {
  align-self: stretch;
  position: relative;
  text-align: center;
}
.buyNow {
  position: relative;
  font-weight: 600;
}
.buyNowWrapper {
  background-color: var(--ww);
  width: 165px;
  height: 50px;
  display: flex;
  flex-direction: row;
  padding: var(--padding-3xs);
  box-sizing: border-box;
  align-items: center;
  justify-content: center;
  color: var(--dark-grey);
}
.highwayCoffeeParent,
.main {
  display: flex;
  flex-direction: column;
}
.highwayCoffeeParent {
  width: 580px;
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap-xs);
}
.main {
  position: absolute;
  top: -8px;
  left: -4px;
  width: 1732px;
  height: 1125px;
  padding: var(--padding-201xl) var(--padding-15xl);
  box-sizing: border-box;
  align-items: flex-end;
  justify-content: flex-end;
  background-image: url(/public/main@3x.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top;
}
.shop {
  position: relative;
  cursor: pointer;
}
.shopWrapper {
  width: 67px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.recipes {
  position: relative;
}
.frameParent {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-30xl);
}
.headerChild {
  position: relative;
  width: 500px;
  height: 116px;
  object-fit: cover;
}
.vectorIcon,
.vectorIcon1 {
  position: relative;
  width: 21.88px;
  height: 21.94px;
}
.vectorIcon1 {
  width: 30px;
  height: 30.08px;
}
.aboutParent,
.header {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
}
.aboutParent {
  width: 356.88px;
  align-items: center;
  gap: var(--gap-30xl);
}
.header {
  position: absolute;
  width: calc(100% - 264.13px);
  top: -15px;
  right: 97.13px;
  left: 167px;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  align-items: flex-end;
  gap: var(--gap-141xl);
}
.macbookPro165 {
  position: relative;
  background-color: var(--color-white);
  width: 100%;
  height: 1117px;
  overflow: hidden;
  text-align: left;
  font-size: var(--font-size-5xl);
  color: var(--color-white);
  font-family: var(--font-inter);
}
