@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600;700;800&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --font-inter: Inter;
  --font-montserrat: Montserrat;

  /* font sizes */
  --font-size-5xl: 24px;
  --font-size-13xl: 32px;
  --font-size-xl: 20px;
  --font-size-sm: 14px;
  --font-size-xs: 12px;
  --font-size-3xs: 10px;
  --font-size-5xs: 8px;
  --font-size-base: 16px;

  /* Colors */
  --color-white: #fff;
  --very-light-gray: #f4f4f4;
  --ww: rgba(244, 244, 244, 0.65);
  --color-gray-100: #162835;
  --dark-grey: #171a20;
  --see-through: rgba(0, 0, 0, 0.2);
  --color-black: #000;
  --color-silver: #c4c4c4;
  --color-gainsboro: #dfe1e5;
  --color-darkslategray: #333;

  /* Gaps */
  --gap-141xl: 160px;
  --gap-30xl: 49px;
  --gap-xs: 12px;
  --gap-3xs: 10px;

  /* Paddings */
  --padding-201xl: 220px;
  --padding-15xl: 34px;
  --padding-3xs: 10px;

  /* border radiuses */
  --br-xl: 20px;
  --br-11xl: 30px;
  --br-5xs: 8px;
}
